<template>
  <main class="function__management__main">
    <MenuAddPopup
      :popupEventFlag="popupEventFlag"
      @popupClose="popupToggle"
      :popupState="popupState"
      :selectedObj="selectedRowObj"
      :menuList="getMenuList"
      @refreshList="getMenus"
    />
    <section class="box__shadow__01 vertical__scroll grid__wrap">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["메뉴 관리"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["메뉴 관리"].subTitle }}
      </h5>
      <div class="add__or__search__wrap">
        <ul>
          <li>
            <dl>
              <dt>상위메뉴</dt>
              <dd>
                <select
                  name=""
                  id=""
                  v-model="menuHighIdFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option
                    v-for="menu in menuList"
                    :key="menu.index"
                    :value="menu.menuId"
                  >
                    {{ menu.menuNm }}
                  </option>
                </select>
              </dd>
            </dl>
          </li>
          <li>
            <dl>
              <dt>사용여부</dt>
              <dd>
                <select
                  name=""
                  id=""
                  ref="useYnSelector"
                  v-model="useYnFilter"
                  @change="gridDataFiltering"
                >
                  <option value="">전체</option>
                  <option value="사용">사용</option>
                  <option value="미사용">미사용</option>
                </select>
              </dd>
            </dl>
          </li>
        </ul>
        <button class="custom__btn" @click="popupToggle('추가')">
          <span>
            <img src="@/assets/images/icon/ic-plus.svg" alt="추가 아이콘" />
          </span>
          <p>메뉴 추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="filteredGridData"
        ></Grid>
      </div>
    </section>
  </main>
</template>
<script>
import "@/assets/css/functionManagement.css";
import { Grid } from "gridjs-vue";
import MenuAddPopup from "@/components/SystemManagement/MenuManagement/MenuAddPopup";
import { getMenuList } from "@/api/menu";

export default {
  components: {
    Grid,
    MenuAddPopup,
  },
  data() {
    return {
      popupEventFlag: "Close",
      cols: [
        { name: "번호", id: "index" },
        { name: "상위메뉴", id: "menuHighNm" },
        { name: "메뉴명", id: "menuNm" },
        { name: "메뉴ID", id: "menuId" },
        { name: "링크", id: "link" },
        { name: "기능", id: "funcNm" },
        { name: "상태", id: "useYn" },
        { name: "등록자명", id: "registerName" },
        { name: "변경일시", id: "updateDate" },
      ],
      menuList: [],
      allMenuList: [],
      menuHighIdFilter: "",
      useYnFilter: "사용",
      selectedRowObj: "",
      popupState: "",
      filteredGridDataBack: [],
      filteredGridData: [],
    };
  },
  computed: {
    getMenuList() {
      //return this.$store.state.systemManagement.menuManagement;
      return this.menuList;
    },
  },
  mounted() {
    this.getMenus();
  },
  methods: {
    popupToggle(evtName) {
      if (evtName === "추가") this.selectedRowObj = false;
      this.popupState = evtName;
      this.popupEventFlag === "Close"
        ? (this.popupEventFlag = "Open")
        : (this.popupEventFlag = "Close");
    },
    rowClick(param) {
      const selectedObjIndex = Number(param.path[1].childNodes[0].textContent);
      const selectedCell = param.path[0].getAttribute("data-column-id");
      if (selectedCell === "menuId") this.popupToggle("수정");
      this.selectedRowObj = { ...this.menuList[selectedObjIndex - 1] };
      if (this.$env == "development") console.log(this.menuList);
      if (this.$env == "development") console.log(this.selectedRowObj);
    },
    getMenus() {
      getMenuList().then((response) => {
        this.menuList = response.data.response.data;
        this.menuList = this.menuList.filter(
          (obj) => obj.funcNm !== "앱 로그 조회"
        ); // 앱 로그 조회 임시 필터링 22.09.02
        this.menuList.forEach(function (object, index) {
          object.index = index + 1;
          if (object.useYn === "Y") {
            object.useYn = "사용";
          } else {
            object.useYn = "미사용";
          }
        });
        this.filteredGridDataBack = this.menuList;
        if (this.menuHighIdFilter === "" && this.useYnFilter === "사용") {
          this.filteredGridData = this.menuList;
          this.filteredGridData = this.filteredGridData.filter(
            (obj) => obj.useYn.trim() === "사용"
          );
        } else {
          this.gridDataFiltering();
        }
      });
      /*this.$store.state.systemManagement.menuManagement.map(
        (menu, index) => (menu.index = index + 1)
      );
      this.menuList = this.$store.state.systemManagement.menuManagement;*/
    },
    gridDataFiltering() {
      this.filteredGridData = this.filteredGridDataBack;

      if (this.menuHighIdFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.menuHighId.trim() === this.menuHighIdFilter
        );
      }
      if (this.useYnFilter !== "") {
        this.filteredGridData = this.filteredGridData.filter(
          (obj) => obj.useYn.trim() === this.useYnFilter
        );
      }
    },
  },
};
</script>
