<template>
  <div class="popup__wrap" :class="{ on: popupEventFlag === 'Open' }">
    <div class="popup__bg"></div>
    <div class="common__popup box__shadow__02">
      <div class="popup__top">
        <h4>메뉴 {{ popupState }}</h4>
        <button class="pop__close__btn" @click="popupCloseClick"></button>
      </div>
      <ul>
        <li v-show="popupState === '수정'">
          <dl>
            <dt class="f__medium">메뉴ID</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqMenu.menuId"
                :disabled="popupState === '수정'"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">메뉴명</dt>
            <dd>
              <input
                type="text"
                class="custom__input"
                v-model="reqMenu.menuNm"
              />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">상위메뉴</dt>
            <dd>
              <select name="" id="" v-model="reqMenu.menuHighId">
                <option value="">상위메뉴없음</option>
                <option
                  v-for="menu in menuList"
                  :key="menu.index"
                  :value="menu.menuId"
                >
                  {{ menu.menuNm }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">링크</dt>
            <dd>
              <input type="text" class="custom__input" v-model="reqMenu.link" />
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">기능매칭</dt>
            <dd>
              <select name="" id="" v-model="reqMenu.funcId">
                <option value="">선택</option>
                <option
                  v-for="func in allFunctionList"
                  :key="func.index"
                  :value="func.funcId"
                >
                  {{ func.funcNm }}
                </option>
              </select>
            </dd>
          </dl>
        </li>
        <li>
          <dl>
            <dt class="f__medium">사용여부</dt>
            <dd>
              <select name="" id="" v-model="reqMenu.useYn">
                <option value="">선택</option>
                <option value="Y">사용</option>
                <option value="N">미사용</option>
              </select>
            </dd>
          </dl>
        </li>
      </ul>
      <div class="custom__btn__wrap">
        <button class="custom__btn delete" @click="removeOrClean">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>삭제</p>
        </button>
        <button class="custom__btn" @click="addMenu">
          <span>
            <img src="@/assets/images/icon/ic-save.svg" alt="저장 아이콘" />
          </span>
          <p>저장</p>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import { addMenu, updateMenu, removeMenu } from "@/api/menu";
import { getFunctionList } from "@/api/function";
import { clear } from "@/utill/commom";
export default {
  props: {
    popupEventFlag: String,
    popupState: String,
    selectedObj: [Object, Boolean],
    menuList: Array,
  },
  data() {
    return {
      reqMenu: {
        menuHighId: "",
        menuNm: "",
        menuId: "",
        link: "",
        funcId: "",
        useYn: "Y",
      },
      allFunctionList: [],
    };
  },
  created() {
    getFunctionList().then((response) => {
      this.allFunctionList = response.data.response.data;
    });
  },
  watch: {
    popupState() {
      if (this.popupState === "수정") {
        this.reqMenu = this.selectedObj;
        if (this.selectedObj.useYn === "사용") {
          this.reqMenu.useYn = "Y";
        } else {
          this.reqMenu.useYn = "N";
        }
      } else if (this.popupState === "추가") {
        clear(this.reqMenu);
      }
    },
  },
  methods: {
    popupCloseClick() {
      this.$emit("popupClose", "Close");
    },
    addMenu() {
      if (this.validateMenu()) {
        if (this.$env == "development") console.log(this.reqMenu);
        if (this.popupState === "수정") {
          updateMenu(this.reqMenu)
            .then((response) => {
              if (response.data.status === "OK") {
                this.$toast.success("메뉴 수정이 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        } else {
          addMenu(this.reqMenu)
            .then((response) => {
              if (this.$env == "development") console.log(response);
              if (response.data.status === "OK") {
                this.$toast.success("메뉴 추가가 완료되었습니다");
                this.popupCloseClick();
                this.$emit("refreshList");
              }
            })
            .catch((error) => this.$toast.error(error.message));
        }
      }
    },
    validateMenu() {
      /*if (this.reqMenu.menuId === "" || this.reqMenu.menuId === null) {
        this.$toast.error("코드 아이디를 입력하세요.");
        return false;
      }*/
      if (this.reqMenu.menuNm === "" || this.reqMenu.menuNm === null) {
        this.$toast.error("메뉴 명을 입력하세요.");
        return false;
      }
      if (this.reqMenu.funcId === "" || this.reqMenu.basePafuncIdth === null) {
        this.$toast.error("매칭기능을 입력하세요.");
        return false;
      }
      if (this.reqMenu.useYn === "" || this.reqMenu.useYn === null) {
        this.$toast.error("사용여부를 입력하세요.");
        return false;
      }
      return true;
    },
    removeOrClean() {
      if (this.popupState === "수정") {
        removeMenu(this.reqMenu.menuId)
          .then((response) => {
            if (this.$env == "development") console.log(response);
            if (response.data.status === "OK") {
              this.$toast.success("메뉴가 삭제되었습니다");
              this.popupCloseClick();
              this.$emit("refreshList");
            }
          })
          .catch((error) => this.$toast.error(error.message));
      } else if (this.popupState === "추가") {
        this.clear();
      }
    },
    clear() {
      clear(this.reqMenu);
    },
  },
};
</script>
